@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2");
  src: url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2");
  src: url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  font-style: normal;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
.sprite {
  display: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.button {
  display: inline-block;
  width: 100%;
  padding-top: 14px;
  padding-bottom: 14px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  font-style: normal;
  color: #444444;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  background-color: #ffffff;
  border: 2px solid #444444;
}
.button:hover,
.button:focus {
  border-color: #444444;
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  border-color: #444444;
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
.button__review {
  padding-top: 16px;
  padding-bottom: 16px;
  border: 1px solid #f9f9f9;
  font-size: 0;
}
.button__review--prev {
  border-left: none;
  background-image: url("../img/icon-left-arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.button__review--next {
  background-image: url("../img/icon-right-arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.button__review:hover,
.button__review:focus {
  background-color: #ffffff;
  opacity: 0.6;
}
.button__review:active {
  background-color: #ffffff;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .button__review {
    border: none;
    padding-top: 25px;
  }
}
.header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
}
.header__toggle {
  position: absolute;
  top: 27px;
  right: 29px;
  z-index: 2;
  display: block;
  width: 20px;
  height: 20px;
  background: inherit;
  border: none;
  font-size: 0;
}
.header--closed .header__toggle {
  background-image: url("../img/icon-menu-open.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.header--opened .header__toggle {
  background-image: url("../img/icon-menu-close.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 767px) {
  .header--closed .navigation {
    display: none;
  }
  .header--nojs .header__toggle {
    display: none;
  }
  .header--nojs .navigation--main,
  .header--nojs .navigation--user {
    display: block;
  }
}
@media (min-width: 768px) {
  .header {
    justify-content: space-between;
    width: 668px;
    margin: 0 auto;
  }
  .header__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .header {
    width: 1050px;
    margin: 0 auto;
  }
}
.logo {
  margin: 0 auto;
}
.logo--header {
  margin: 19px auto;
}
.logo--header:hover,
.logo--header:focus {
  opacity: 0.6;
}
.logo--header:active {
  opacity: 0.3;
}
.logo--footer {
  display: none;
}
.logo--footer:hover path,
.logo--footer:focus path {
  fill: #46c1ae;
}
.logo--footer:active path {
  fill: #46c1ae;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .logo {
    margin-left: 0;
    margin-right: 0;
  }
  .logo--header {
    order: -2;
  }
  .logo--footer {
    display: block;
    width: 33%;
    margin: 0;
    padding-top: 15px;
  }
  .logo__image {
    width: 111px;
    height: 46px;
  }
}
@media (min-width: 1200px) {
  .logo--header {
    margin-top: 10px;
    margin-bottom: 0;
    order: -1;
  }
  .logo__image {
    width: 149px;
    height: 72px;
  }
}
.navigation {
  width: 100%;
}
.navigation--main {
  border-top: 1px solid #ececec;
}
.navigation--popular {
  display: none;
}
.navigation__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
.navigation__item {
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #ececec;
}
.navigation__item--basket {
  border: none;
}
.navigation__item:hover .navigation__link,
.navigation__item:focus .navigation__link {
  color: rgba(0, 0, 0, 0.6);
}
.navigation__item:hover svg,
.navigation__item:focus svg {
  fill: #63d1bb;
  opacity: 1;
}
.navigation__item:active .navigation__link {
  color: rgba(0, 0, 0, 0.3);
}
.navigation__item:active svg {
  fill: #63d1bb;
  opacity: 0.3;
}
.navigation__link {
  position: relative;
  display: block;
  padding: 27px 0 27px 76px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  font-style: normal;
  color: #000000;
  text-decoration: none;
}
.navigation__link--search svg,
.navigation__link--basket svg {
  position: absolute;
  top: 32px;
  left: 33px;
  fill: #d3d2d2;
}
@media (min-width: 768px) {
  .navigation {
    width: 50%;
  }
  .navigation--popular {
    margin-bottom: -100%;
  }
  .navigation--main {
    border: none;
  }
  .navigation--user {
    width: 348px;
    order: -1;
  }
  .navigation__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .navigation__item {
    border-bottom: none;
  }
  .navigation__item--search {
    border-left: 1px solid #e7e7e7;
  }
  .navigation__item--basket {
    border-left: 1px solid #e7e7e7;
  }
  .navigation__link {
    display: block;
    padding: 31px 0;
    font-family: "Open Sans", "Arial", sans-serif;
    font-weight: bold;
    font-size: 15px;
    line-height: 24px;
    font-style: normal;
    color: #000000;
    text-decoration: none;
  }
  .navigation__link--main {
    width: 167px;
    padding: 20px 0;
  }
  .navigation__link--search {
    width: 86px;
    padding: 44px 36px;
    font-size: 0;
    line-height: 0;
  }
  .navigation__link--basket {
    padding-left: 78px;
    font-size: 17px;
  }
  .navigation__link--search svg {
    top: 36px;
    left: 34px;
  }
  .navigation__link--basket svg {
    top: 35px;
    left: 30px;
  }
}
@media (min-width: 1200px) {
  .navigation {
    width: 42%;
  }
  .navigation--user {
    order: -1;
  }
  .navigation--main {
    order: -2;
  }
  .navigation--popular {
    position: absolute;
    top: 100%;
    display: block;
    width: 50%;
  }
  .navigation__item {
    border-bottom: none;
  }
  .navigation__link {
    font-family: "Open Sans", "Arial", sans-serif;
    font-weight: bold;
    font-size: 17px;
    line-height: 24px;
    font-style: normal;
  }
  .navigation__link--main {
    padding: 32px 0;
  }
  .navigation__link--popular {
    padding: 20px 0;
    font-family: "Open Sans", "Arial", sans-serif;
    font-weight: bold;
    font-size: 15px;
    line-height: 24px;
    font-style: normal;
    color: #000000;
    text-decoration: none;
  }
  .navigation__link--catalog {
    width: 185px;
    border-right: 1px solid #e7e7e7;
  }
  .navigation__link--new {
    width: 185px;
  }
  .navigation__link--knitting,
  .navigation__link--sale {
    width: 200px;
    padding-left: 36px;
  }
  .navigation__link--search {
    font-size: 0;
    padding: 32px 36px;
  }
  .navigation__link--basket {
    width: 266px;
    padding: 32px 0 32px 84px;
  }
  .navigation__list--user {
    justify-content: flex-end;
  }
}
.delivery {
  display: none;
}
@media (min-width: 768px) {
  .delivery {
    position: absolute;
    right: 0;
    top: 100%;
    display: block;
    width: 50%;
  }
  .delivery__description {
    display: inline-block;
    padding: 20px 0;
    padding-left: 103px;
    margin: 0;
    font-family: "Open Sans", "Arial", sans-serif;
    font-weight: normal;
    font-size: 15px;
    line-height: 24px;
