.filter {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 0;
  border: none;
  border-top: 1px solid #62d1ba;
}
.filter--phone {
  margin-bottom: 31px;
}
.filter__title {
  margin: 0;
  margin-bottom: 23px;
  padding: 0;
  padding-right: 28px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  font-style: normal;
  color: #63d1bb;
  text-transform: uppercase;
}
.filter__label {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
  font-style: normal;
}
.filter__label--radio {
  padding: 12px 0 12px 63px;
}
.filter__label--checkbox {
  padding: 13px 0 13px 62px;
  margin-bottom: 17px;
}
.filter__label--checkbox:last-child {
  margin-bottom: 0;
}
.filter__label--person {
  padding-top: 5px;
  margin-bottom: 17px;
}
.filter__label--person:last-child {
  margin-bottom: 0;
}
.filter__label--phone svg {
  fill: #cccccc;
  position: absolute;
  top: -5px;
  right: 6px;
}
.filter__label--mail svg {
  fill: #cccccc;
  position: absolute;
  top: -5px;
  right: 2px;
}
.filter__label--textarea {
  margin: 0;
}
.filter__label:hover svg,
.filter__label:focus svg {
  fill: #62d1ba;
}
.filter__label:active svg {
  fill: #62d1ba;
}
.filter__input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
}
.filter__radio {
  position: absolute;
  top: 4px;
  left: 1px;
  width: 39px;
  height: 39px;
  border-radius: 50%;
  border: 1px solid #484848;
}
.filter__input:checked + .filter__radio {
  background-color: #62d1ba;
  box-shadow: inset 0 0 0 11px #ffffff;
}
.filter__input:hover + .filter__radio,
.filter__input:focus + .filter__radio {
  border-color: #62d1ba;
}
.filter__input:active + .filter__radio {
  border-color: #c7c7c7;
}
.filter__color {
  display: flex;
  flex-direction: column;
}
.filter__box {
  position: absolute;
  top: 2px;
  left: 1px;
  width: 39px;
  height: 39px;
  border: 1px solid #484848;
