.modal {
  display: none;
  position: fixed;
  top: 25%;
  width: 100%;
  z-index: 11;
}
.modal--show {
  display: block;
}
.modal__wrapper {
  width: 100%;
  margin: 0 auto;
  padding-top: 30px;
  border: 1px solid #c7c7c7;
  background-color: #ffffff;
  box-shadow: 0 10px 15px 0 gray;
