  width: 82px;
  position: relative;
  margin: 0;
  padding: 0;
}
.modal__input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
}
.modal__radio {
  display: block;
  width: 82px;
  padding: 16px 0;
  margin-bottom: 32px;
  border: 2px solid #ececec;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  font-style: normal;
  text-align: center;
}
.modal__input:hover + .modal__radio,
.modal__input:focus + .modal__radio {
  border-color: #c7c7c7;
}
.modal__input:active + .modal__radio {
  border-color: #62d1ba;
}
.modal__input:checked + .modal__radio {
  border-color: #62d1ba;
}
.modal__button {
  margin-bottom: 47px;
  background-color: #63d1bb;
  border-color: #63d1bb;
  color: #ffffff;
