.social {
  min-height: 80px;
  padding: 28px 25px;

  border-bottom: 1px solid #e5e5e5;
}

.social__list {
  display: flex;
  justify-content: center;
  align-items: center;
  .list-style();
}

.social__item {
  width: 47px;
}

.social__link svg {
  fill: #231f20;
}

.social__link {
  position: relative;

  &--ig svg {
    position: absolute;
    top: 2px;
    left: -3px;
  }

  &--fb svg {
    position: absolute;
    top: -3px;
    left: 13px;
  }

  &--tw svg {
    position: absolute;
    top: 2px;
    left: 21px;
  }

  &:hover svg,
  &:focus svg {
    fill: #46c1ae;
  }

  &:active svg {
    fill: #46c1ae;
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .social {
    width: 33%;
    padding: 25px;

    border: none;
  }
}

@media (min-width: @desktop-width) {
  .social {
    padding: 23px 25px;
  }
}
